@import url(https://fonts.googleapis.com/css?family=Raleway:200,700|Source+Sans+Pro:300,600,300italic,600italic);
body,
input,
select,
textarea {
  color: #ffffff;
}

.wrapper {
  background-color: #2e3141;
  margin: 6.5em 0;
  position: relative;
  width: 100%;
  z-index: -1;
}
.wrapper:before {
  box-shadow: inset 0 -1px 0 0 #2e3141, 0 1px 0 0 #2e3141;
}

.wrapper:after {
  box-shadow: inset 0 -1px 0 0 #2e3141, 0 1px 0 0 #2e3141;
}

.wrapper:before,
.wrapper:after {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  content: "";
  display: inline;
  height: 6.5em;
  position: absolute;
  width: 100%;
}

.wrapper:before {
  left: 0px;
  top: -6.5em;
}

.wrapper:after {
  transform: scaleY(-1);
  bottom: -6.5em;
  left: 0;
}
.wrapper .inner {
  padding: 3em 0 1em 0;
  margin: 0 auto;
  width: 55em;
}

.image {
  border-radius: 5px;
  border: 0;
  display: inline-block;
  position: relative;
}

.wrapper.alt:before {
  transform: scaleX(-1);
}

.wrapper.alt:after {
  transform: scaleY(-1) scaleX(-1);
}

p.menutitle {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 125%;
  vertical-align: baseline;
  color: #f1eaea;
  padding-bottom: 0.5em;
  border-bottom: solid 1px whitesmoke;
  font-family: Raleway, Helvetica, sans-serif;
  font-weight: 200;
  letter-spacing: 0.1em;
  line-height: 1.85em;
}

li,
a {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

ul {
  list-style: disc;
  margin: 0 0 2em 0;
  padding-left: 1em;
}

ul li {
  padding-left: 0.5em;
}

a {
  transition: color 0.2s ease-in-out, border-bottom-color 0.2s ease-in-out;
  border-bottom: dotted 1px rgba(255, 255, 255, 0.35);
  color: #ffffff;
}

a:hover {
  border-bottom-color: transparent;
  color: #ffffff;
  text-decoration: none;
}

#menu {
  align-items: center;
  display: flex;
  justify-content: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: rgba(46, 49, 65, 0.9);
  cursor: default;
  height: 100%;
  left: 0;
  position: fixed;
  text-align: center;
  top: 0;
  width: 100%;
}

#menu .links {
  list-style: none;
  margin-bottom: 1.5em;
  padding: 0;
}

#menu .links li {
  padding: 0;
}

#menu .links li a {
  border-radius: 5px;
  border: 0;
  display: block;
  font-family: Raleway, Helvetica, sans-serif;
  font-size: 0.8em;
  font-weight: 200;
  letter-spacing: 0.1em;
  line-height: 1.85em;
  padding: 0.75em 0;
  text-transform: uppercase;
}

#menu .links li a:hover {
  background: #151516;
}

.fadeOut {
  opacity: 0;
  width: 0;
  height: 0;
  transition-property: all;
  transition-duration: 1s;
  visibility: hidden;
}
.fadeIn {
  opacity: 1;
  width: 100px;
  height: 100px;
  transition-property: all;
  transition-duration: 1s;
  visibility: visible;
}

#header {
  background: transparent;
  height: 3em;
  left: 0;
  line-height: 1.75em;
  padding: 0.5em 1.25em;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
}

button.menu {
  font-family: Raleway, Helvetica, sans-serif;
  font-weight: 600;
  letter-spacing: 0.1em;
  position: absolute;
  right: 0.7em;
  background-color: rgba(0, 0, 0, 0.123);
  border: 1px solid whitesmoke;
  border-radius: 6px;
  border-style: hidden hidden solid hidden;
  color: white;
  padding: 0px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  text-transform: uppercase;
  transition-property: all;
  transition-duration: 0.8s;
  opacity: 0.4;
}
button.menu:focus {
  outline: none;
}
button.menu:hover {
  opacity: 1;
}

#banner {
  padding: 8em 0 4.75em 0;
  margin: 0 auto;
  width: 55em;
}

h2 {
  opacity: 1;
  border-bottom: solid 2px rgba(255, 255, 255, 0.125);
  font-size: 2.25em;
  margin-bottom: 0.8em;
  padding-bottom: 0.4em;
}

p {
  opacity: 1;
  font-family: Raleway, Helvetica, sans-serif;
  font-size: 1em;
  font-weight: 200;
  letter-spacing: 0.1em;
  line-height: 2;
}

.wrapper.spotlight {
  background-color: #4c5c96;
}

.wrapper.spotlight:before,
.wrapper.spotlight:after {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='100' height='100' viewBox='0 0 100 100' preserveAspectRatio='none'%3E%3Cpolygon points='0,100 100,0 100,100' style='fill:%234c5c96%3B' /%3E%3C/svg%3E");
}

.wrapper.spotlight:before {
  box-shadow: inset 0 -1px 0 0 #4c5c96, 0 1px 0 0 #4c5c96;
}

.wrapper.spotlight:after {
  box-shadow: inset 0 -1px 0 0 #3a59c7, 0 1px 0 0 #4c5c96;
}

.wrapper.spotlight .inner {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.wrapper.spotlight .image {
  border-radius: 100%;
  margin: 0 3em 2em 0;
  width: 22em;
  overflow: hidden;
}

.wrapper.spotlight .image img {
  border-radius: 100%;
  width: 100%;
}

.wrapper.spotlight .content {
  width: 100%;
}

.wrapper.spotlight.style2 {
  background-color: #45558d;
}

.wrapper.spotlight.style2:before,
.wrapper.spotlight.style2:after {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='100' height='100' viewBox='0 0 100 100' preserveAspectRatio='none'%3E%3Cpolygon points='0,100 100,0 100,100' style='fill:%2345558d%3B' /%3E%3C/svg%3E");
}

.wrapper.spotlight.style2:before {
  box-shadow: inset 0 -1px 0 0 #45558d, 0 1px 0 0 #45558d;
}

.wrapper.spotlight.style2:after {
  box-shadow: inset 0 -1px 0 0 #45558d, 0 1px 0 0 #45558d;
}

.wrapper.spotlight.style1 {
  background-color: #3f4e85;
}

.wrapper.spotlight.style1:before,
.wrapper.spotlight.style1:after {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='100' height='100' viewBox='0 0 100 100' preserveAspectRatio='none'%3E%3Cpolygon points='0,100 100,0 100,100' style='fill:%233f4e85%3B' /%3E%3C/svg%3E");
}

.wrapper.spotlight.style1:before {
  box-shadow: inset 0 -1px 0 0 #3f4e85, 0 1px 0 0 #3f4e85;
}

.wrapper.spotlight.style1:after {
  box-shadow: inset 0 -1px 0 0 #3f4e85, 0 1px 0 0 #3f4e85;
}

.wrapper.spotlight.alt .inner {
  flex-direction: row-reverse;
  text-align: right;
}

.wrapper.spotlight.alt .image {
  margin: 0 0 2em 3em;
}

.wrapper.style1 {
  background-color: #454858;
}

.wrapper.style1:before,
.wrapper.style1:after {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='100' height='100' viewBox='0 0 100 100' preserveAspectRatio='none'%3E%3Cpolygon points='0,100 100,0 100,100' style='fill:%23454858%3B' /%3E%3C/svg%3E");
}

.wrapper.style1:before {
  box-shadow: inset 0 -1px 0 0 #454858, 0 1px 0 0 #454858;
}

.wrapper.style1:after {
  box-shadow: inset 0 -1px 0 0 #454858, 0 1px 0 0 #454858;
}

article {
  padding: 1.75em 1.75em 0.1em 1.75em;
  background-color: #353849;
  border-radius: 5px;
  margin: 1.5em 3em 1.5em 0;
  width: 80%;
  display: inline-block;
  margin-left: 1em;
}

article .image {
  border-radius: 5px 5px 0 0;
  display: block;
  margin-bottom: 1.75em;
  margin-left: -1.75em;
  margin-top: -1.75em;
  position: relative;
  width: calc(100% + 3.5em);
}

article .image img {
  border-radius: 5px 5px 0 0;
  width: 100%;
}

a.special {
  text-decoration: none;
  border-bottom: 0;
  display: block;
  font-family: Raleway, Helvetica, sans-serif;
  font-size: 0.8em;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin: 0 0 2em 0;
  text-transform: uppercase;
}

a.special:before {
  display: inline-block;
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  text-transform: none !important;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

a.special:before {
  transition: background-color 0.2s ease-in-out;
  border-radius: 100%;
  border: solid 2px rgba(255, 255, 255, 0.125);
  content: "\f105";
  display: inline-block;
  font-size: 1.25em;
  height: 2em;
  line-height: 1.75em;
  margin-right: 0.85em;
  text-align: center;
  text-indent: 0.15em;
  vertical-align: middle;
  width: 2em;
}

a.special:hover:before {
  background-color: rgba(255, 255, 255, 0.025);
}

a.special:active:before {
  background-color: rgba(255, 255, 255, 0.075);
}

#footer .inner {
  padding: 5em 0 3em 0;
  display: -moz-flex;
  display: -ms-flex;
  display: flex;
  -moz-flex-direction: row;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 55em;
}

#footer .inner > * {
  width: 100%;
}

#footer .inner .contact {
  width: calc(50% - 1.5em);
}

